@import url(https://fonts.googleapis.com/css?family=DM+Serif+Display&display=swap);
body {
  background-color: #f7f7f7;
  margin: 0px; }

a {
  text-decoration: none; }

#App {
  padding: 45px 0; }
  #App .header {
    background-color: #f7f7f7;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    display: flex;
    height: 45px;
    justify-content: space-between;
    padding: 0 15px;
    position: fixed;
    text-align: center;
    top: 0px;
    width: calc(100% - 30px);
    z-index: 100; }
    #App .header img {
      height: 20px;
      margin: 12px 0; }
    #App .header h3 {
      font-family: 'DM Serif Display', serif;
      color: #3d3d3d;
      margin: 10px 0; }
  #App .news-grids {
    display: flex;
    flex-wrap: wrap;
    font-family: sans-serif;
    justify-content: space-evenly;
    text-align: center; }
    #App .news-grids .waterfall {
      flex-grow: 0;
      flex-shrink: 0;
      flex-basis: calc(50% - 20px);
      margin: 0px 10px; }
      #App .news-grids .waterfall .news-grid {
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        margin: 20px 0px;
        text-decoration: none;
        overflow: hidden;
        width: 100%; }
        #App .news-grids .waterfall .news-grid img {
          width: 100%; }
        #App .news-grids .waterfall .news-grid .news-title {
          color: #000;
          font-size: 12px;
          padding: 0 10px;
          text-align: left; }
    #App .news-grids .news-status {
      font-size: 15px;
      margin-bottom: 20px;
      width: 100%; }
      #App .news-grids .news-status.loading:after {
        content: ' .';
        -webkit-animation: dots 1s steps(5, end) infinite;
                animation: dots 1s steps(5, end) infinite; }
  #App .footer {
    background-color: #f7f7f7;
    bottom: 0px;
    display: flex;
    height: 45px;
    padding: 5px;
    position: fixed;
    width: 100%; }
    #App .footer .icon {
      flex: 0 0 20px;
      margin: 12px 15px;
      text-align: center; }
      #App .footer .icon svg {
        height: 20px; }
    #App .footer .search-bar {
      flex-grow: 0;
      flex-shrink: 0;
      flex-basis: calc(100% - 130px);
      padding: 0 10px;
      position: relative; }
      #App .footer .search-bar .search-input {
        border: none;
        border-radius: 20px;
        height: 24px;
        margin: 10px 0;
        width: 100%; }
      #App .footer .search-bar .icon {
        position: absolute;
        right: 10px;
        top: 0px; }
        #App .footer .search-bar .icon svg {
          height: 12px;
          width: 12px; }

@-webkit-keyframes dot-keyframes {
  0% {
    opacity: .4;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1); }
  50% {
    opacity: 1;
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2); }
  100% {
    opacity: .4;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1); } }

@keyframes dot-keyframes {
  0% {
    opacity: .4;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1); }
  50% {
    opacity: 1;
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2); }
  100% {
    opacity: .4;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1); } }

.loading-dots {
  text-align: center;
  margin-bottom: 20px;
  width: 100%; }
  .loading-dots--dot {
    -webkit-animation: dot-keyframes 1.5s infinite ease-in-out;
            animation: dot-keyframes 1.5s infinite ease-in-out;
    background-color: #000;
    border-radius: 10px;
    display: inline-block;
    height: 10px;
    width: 10px; }
    .loading-dots--dot:nth-child(2) {
      -webkit-animation-delay: .5s;
              animation-delay: .5s;
      margin: 0 10px; }
    .loading-dots--dot:nth-child(3) {
      -webkit-animation-delay: 1s;
              animation-delay: 1s; }

